@use "../base";
@use "../header";
@use "../footer";
@use "../main";

body {
    background-color: aliceblue;
    color: midnightblue;
    font-size: 1.6rem;
    display: grid;
    grid-template-rows: auto 1fr auto;
    height: 100vh;
}

.site-content {
    padding: 2rem;
}

.site-header {
    background-color: rgba(black, 0.1);
    padding: 1rem;
}

.site-footer {
    background-color: rgba(black, 0.1);
    padding: 1rem;
}